import {
  Box,
  Flex,
  Text,
  IconButton,
  Button,
  Stack,
  useColorModeValue,
  useBreakpointValue,
  useDisclosure,
  VStack,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerOverlay,
  Drawer,
} from "@chakra-ui/react";
import { HamburgerIcon, DownloadIcon } from "@chakra-ui/icons";

import Skills from "./Skills";
import AboutMe from "./AboutMe";
import ContactMe from "./ContactMe";
import Projects from "./Projects";
import Home from "./Home";
import { useRef } from "react";
import Resume from "./Resume";

export default function WithSubnavigation() {
  return (
    <Box
      style={{
        position: "sticky",
        top: 0,
        backgroundColor: "rgb(255,255,255)",
        zIndex: 1,
      }}
    >
      <Flex
        bg={useColorModeValue("white", "gray.800")}
        color={useColorModeValue("gray.600", "white")}
        minH={"60px"}
        py={{ base: 2 }}
        px={{ base: 4 }}
        borderBottom={1}
        borderStyle={"solid"}
        borderColor={useColorModeValue("gray.200", "gray.900")}
        align={"center"}
      >
        <Flex flex={{ base: 1 }} justify={{ base: "start", md: "start" }}>
          <Text
            textAlign={useBreakpointValue({ base: "center", md: "left" })}
            fontFamily={"heading"}
            color={useColorModeValue("gray.800", "white")}
            ml={5}
          >
            Logo
          </Text>

          <Flex display={{ base: "none", md: "center" }} ml={380}>
            <DesktopNav />
          </Flex>
        </Flex>

        <Button
          display={{ base: "none", md: "inline-flex" }}
          fontSize={"md"}
          fontWeight={600}
          color={"white"}
          colorScheme="messenger"
          variant="solid"
          borderRadius={4}
          href={"#"}
          _hover={{
            backgroundColor: "messenger.600",
          }}
          ml={300}
          mr={4}
        >
          Resume
          <span>
            <DownloadIcon w={5} h={5} color="yellow" />
          </span>
        </Button>

        <Flex
          flex={{ base: "flex", sm: "auto", md: "auto" }}
          ml={{ base: 40 }}
          display={{ base: "flex", md: "none" }}
        >
          <MobileNav />
        </Flex>
      </Flex>
    </Box>
  );
}

const DesktopNav = () => {
  return (
    <Stack direction={"row"} spacing={8}>
      <Home />
      <AboutMe />
      <Projects />
      <Skills />
      <ContactMe />
    </Stack>
  );
};

const MobileNav = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const btnRef = useRef();
  return (
    <Stack
      bg={useColorModeValue("white", "gray.800")}
      p={4}
      //display={{ md: "none" }}
      ml={80}
    >
      <Box display={{ base: "flex", md: "none" }}>
        <IconButton
          aria-label="hamburger"
          icon={<HamburgerIcon />}
          ref={btnRef}
          colorScheme="teal"
          onClick={onOpen}
          variant={"ghost"}
          w={3}
          h={3}
          fontSize={25}
        />

        <Drawer
          isOpen={isOpen}
          placement="right"
          onClose={onClose}
          finalFocusRef={btnRef}
        >
          <DrawerOverlay />
          <DrawerContent
            style={{
              backgroundColor: "rgb(14,36,49)",
              marginTop: "60px",
            }}
          >
            <DrawerCloseButton color="white" />

            <DrawerBody>
              <VStack
                style={{
                  width: "100px",
                  height: "160px",
                  marginTop: "10px",
                  color: "white",
                }}
              >
                <Box>
                  <Box>
                    <Home />
                  </Box>
                  <Box mt="1rem">
                    <AboutMe />
                  </Box>
                  <Box mt="1rem">
                    <Projects />
                  </Box>
                  <Box mt="1rem">
                    <Skills />
                  </Box>
                  <Box mt="1rem">
                    <Resume width="5rem" colorScheme="facebook" />
                  </Box>
                  <Box mt="1rem">
                    <ContactMe />
                  </Box>
                </Box>
              </VStack>
            </DrawerBody>
          </DrawerContent>
        </Drawer>
      </Box>
    </Stack>
  );
};
